const decode = (data) => {
  try {
    //  Base64 
    const reversedData = reverse(data.slice(0, -5)); //  5  reverse
    const base64Decoded = atob(reversedData); //  decode base64
    return decodeURIComponent(escape(base64Decoded)); //  URI encoding
  } catch (error) {
    console.error("Error decoding data:", error);
    return null; //  null 
  }
};

function reverse(s) {
  return s.split('').reverse().join(''); //  reverse string
}

export default {
  decode
};
