<template>
  <v-container class="viewDashboard">
    <v-layout row justify-center>
      <!-- Loading Dialog -->
      <v-dialog v-model="loading" persistent fullscreen content-class="loading-dialog">
        <v-container fill-height>
          <v-layout row justify-center align-center>
            <v-progress-circular
              indeterminate
              :size="80"
              :width="10"
              color="#6ab9eB"
            ></v-progress-circular>
          </v-layout>
        </v-container>
      </v-dialog>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      loading: true, //  loading 
    };
  },
  mounted() {
    this.showLoadingAndRedirect();
  },
  methods: {
    showLoadingAndRedirect() {
      //  loading  3   redirect ()
      setTimeout(() => {
        this.loading = false; //  loading dialog
        window.location.href = 'http://lake_backend.psu.ac.th:5001/'; // Redirect  URL 
      }, 3000); //  3  (3000 milliseconds)
    },
  },
};
</script>

<style scoped>
.viewDashboard {
  height: 100% !important;
}

.loading-dialog {
  background: transparent !important; /*  loading  */
}
</style>
